// main: ../style.less

.modal  {
    display: flex;
    width: 100%;
    height: 100%;
    padding: 0 1rem;
    overflow: scroll;
    background-color: rgba(37, 28, 66, 0.7);

    position: fixed;
    top: 0;
    left: 0;
    z-index: 10000;
}

.modal__dialog {
    width: 100%;
    max-width: 1200px;
    margin: auto;
    position: relative;

    background-color: #fff;

    &--sm {
        max-width: 900px;
    }

}

.modal__close {
    width: 34px;
    height: 39px;
    padding: 0;
    background: none;
    border: 0;
    cursor: pointer;
    position: absolute;
    top: .9rem;
    right: .9rem;
    z-index: 1;
    transition: transform .2s linear;


&:hover {
    transform: rotate(90deg);
}
}